// ==================================================
// > SHADOWBOX
// ==================================================
.shadowbox {
    @include cover;
    position: fixed;
    background: rgba(black, .75);
    z-index: 9999;
    display: none;
    .content {
        @include cover;
        display: flex;
        align-items: center;
        justify-content: center; }
    .close {
        position: absolute;
        top: 20px;
        right: 20px;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        color: white;
        font-size: 30px;
        font-size: 30rem;
        text-indent: -9999px;
        cursor: pointer;
        transition: all .25s ease;
        z-index: 100;
        &:hover {
            color: $main; }
        &:before {
            content: "\f057";
            font-family: $font-fa;
            @include cover;
            text-indent: 0; } } }





