// ==================================================
// > TABLES
// ==================================================
table {
    width: 100%;
    max-width: 100%;
    text-align: center;
    margin: 1em 0;
    border: 1px solid $main;

    // ========== TABLE MODIFIERS ========== //
    &.aligncenter {
        margin: 0 auto; }

    &.alignleft {
        float: left;
        margin-right: 10px; }

    &.full-width {
        width: 100% !important;
        height: auto; }

    &.full-height {
        height: 100%;
        width: auto; }



    td, th {
        padding: 10px; }

    // ========== TH ========== //
    th {
        background: $main;
        color: white;
        &.left-strong-border {
            border-left: 1px solid darken($main, 15%); }

        &.bg-white {
            color: $dgry; }
        p {
            margin: .5em 0; } }

    tfoot {
        background: $lgry;
        th {
            background: none;
            color: $dgry; } }

    // ========== TD ========== //
    td {
        border: 1px solid darken($lgry, 5%);
        &.left-strong-border {
            border-left-color: darken($lgry, 50%); }
        p {
            margin: 0; } }


    // ========== TABLE FILTERS ========== //
    .table-filters {
        td {
            background: #eee;
            padding: 10px;
            height: 30px;
            input, select, .select2-selection--single {
                height: 30px;
                background: white;
                margin: 0; }
            input {
                position: relative;
                top: 1px;
                padding: 0 6px;
                font-size: 12px;
                font-size: 12rem;
                float: none;
                &.submit {
                    top: 2px;
                    background: $main;
                    line-height: 30px;
                    display: block;
                    margin-top: 0px !important;
                    color: white; } }
            .select2-container {
                .select2-selection__rendered,
                .select2-selection__arrow {
                    line-height: 30px;
                    height: 30px; }
                .select2-selection__rendered {
                    padding-left: 5px;
                    padding-right: 5px;
                    font-size: 12px;
                    font-size: 12rem; }
                .select2-selection__arrow {
                    width: 20px; } } } } }



